<template>
    <div class="log-body-x">
        <div class="log-body"></div>
        <van-row style="padding-top:15rem">
            <van-col span="4" offset="11">
                <img style="width:40% ; border-radius:50%" src="../assets/images/icon.jpg" />
            </van-col>
        </van-row>
        <van-row>
            <van-col span="3" offset="11">
                <p style="color:#FFFFFF;font-size:1.5rem;">Hello word!</p>
            </van-col>
        </van-row>
        <van-row>
            <van-col span="6" offset="9">
                <hr />
                <p style="color:#FFFFFF;font-size:1rem;">你是我穷极一生做不完的梦，而我只是你一念之间吹过的风</p>
                <p style="color:#FFFFFF;font-size:1rem; padding-left:90%">— H</p>
            </van-col>
        </van-row>
        <van-row style="padding-top:2rem">
            <van-col span="2" offset="9">
                <van-button
                    @click="Home()"
                    style="background-color:transparent; color:#FFFFFF; border:1px solid #FFFFFF;border-radius:1rem"
                >程序员工具</van-button>
            </van-col>
            <van-col span="1">
                <van-button
                    @click="Gitee()"
                    style=" background-color:transparent; color:#FFFFFF; border:1px solid #FFFFFF;border-radius:1rem"
                >Gitee</van-button>
            </van-col>
            <van-col span="1" style="padding-left:2rem">
                <van-button
                    @click="CSDN()"
                    style=" background-color:transparent; color:#FFFFFF; border:1px solid #FFFFFF;border-radius:1rem"
                >CSDN</van-button>
            </van-col>
            <van-col span="3" style="padding-left:4rem">
                <van-button
                    @click="Email()"
                    style="background-color:transparent; color:#FFFFFF; border:1px solid #FFFFFF;border-radius:1rem"
                >E-mail</van-button>
            </van-col>
        </van-row>
    </div>
</template>
<script>
export default {
    data() {
        return {};
    },
    created() {},
    methods: {
        Home() {
            this.$router.push({ path: "home" });
        },
        Gitee() {
            window.location.href = "https://gitee.com/hezongtan";
        },
        CSDN() {
            window.location.href = "https://me.csdn.net/weixin_42036591";
        },
        Email() {
            this.$toast({
                message: "1993200906@qq.com",
                duration: 2000
            });
        }
    }
};
</script>
<style >
.log-body {
    background: url("../assets/images/logins.jpg");
    background-size: 100% 100%;
    height: 100%;
    position: fixed;
    width: 100%;
    filter: blur(5px);
    z-index: -1;
}
.log-body-x {
    background: url("../assets/images/logins.jpg");
    background-size: 100% 100%;
    height: 100%;
    position: fixed;
    width: 100%;
}
</style>


