import Vue from 'vue'
import Router from 'vue-router'
import Index from '../views/index'
import home from '../views/home'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/home',
      name: 'home',
      component: home
    }
  ]
})



